import React from "react"
import Slider from "react-slick";
import Layout from "../../components/Layout"
import SEO from "../../components/SEO"
import FluidImage from "../../components/FluidImage"
import contentParser from "gatsby-wpgraphql-inline-images"
import Project from "../../components/Sidebar/Project"
import PostForm from '../../components/Form/PostForm'
const Post = ({ pageContext }) => {
  const pluginOptions = {
    wordPressUrl: `https://admin.sharkhung.vn/`,
    uploadsUrl: `https://admin.sharkhung.vn/wp-content/uploads/`,
  }
  const {
    post: { title, content, CustomPost, news_papers },
  } = pageContext
  var settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    speed: 1000,
    slidesToShow: 1,
    fade: false,
    arrows: true,
    slidesToScroll: 1
  };
  console.log(news_papers)
  return (
    <Layout>
      <SEO
        title={title}
        description={title + " Bất động sản Alphaland"}
        keywords={title + " Bất động sản Alphaland, bất động sản HCM, dự án bất động sản HCM"}
      />
      <div className="big-title mb-5">
        <div className="container text-center">
          <h1>{title}</h1>
        </div>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-9 col-xs-12">
            <div className="detail">
              {(() => {
                if (CustomPost.address != null) {
                  return (
                    <>
                      <div className="overview pb-3">
                        <Slider {...settings} className="pb-3 mb-3">
                          {
                            CustomPost.gallery &&
                            CustomPost.gallery.map((prop, index) => (
                              <div key={index}>
                                <FluidImage image={prop} />
                              </div>
                            ))
                          }
                        </Slider>
                        <h3 className="detail-title">Thông tin chính</h3>
                        <ul>
                          <li><strong>{title}</strong></li>
                          {(() => {
                            if (CustomPost.investor != null) {
                              return (
                                <li>Chủ đầu tư: <strong>{CustomPost.investor}</strong></li>
                              )
                            }
                          })()}
                          <li>Địa chỉ: <strong>{CustomPost.address}</strong></li>
                          <li className="price">Giá: từ <strong>{CustomPost.price}</strong></li>
                        </ul>
                      </div>
                      <a href="#form-wrapper" type="button" className="btn btn-warning">Liên hệ</a>
                    </>
                  )
                }
              })()}

              <div className="info pb-3">

                {(() => {
                  if (CustomPost.address === null) {
                    return (
                      <>
                        <div>{contentParser({ content }, pluginOptions)}</div>
                      </>
                    )
                  }
                  else {
                    return (
                      <>
                        <h3 className="detail-title">Giới thiệu</h3>
                        {/* <div dangerouslySetInnerHTML={{ __html: content }} /> */}
                        <div>{contentParser({ content }, pluginOptions)}</div>
                      </>
                    )
                  }
                })()}

              </div>

              {(() => {
                if (CustomPost.location != null) {
                  return (
                    <div className="localtion pb-3 mb-2">
                      <h3 className="detail-title">Vị trí</h3>
                      <div dangerouslySetInnerHTML={{ __html: CustomPost.location }} />

                    </div>
                  )
                }

              })()}

              {(() => {
                if (CustomPost.utility != null) {
                  return (
                    <div className="utilities pb-3">
                      <h3 className="detail-title">Tiện ích xung quanh</h3>
                      <div className="row">
                        {
                          CustomPost.utility &&
                          CustomPost.utility.map((prop, index) => (
                            <div className="col-md-4 col-xs-12" key={index}>
                              <p>
                                <i className="fa fa-hand-o-right" aria-hidden="true"></i> {prop.title}
                              </p>
                            </div>
                          ))
                        }
                      </div>
                    </div>
                  )
                }

              })()}
            </div>
            <div className="news_papers">
              {(() => {
                if (news_papers.newsPapers !== null) {
                  return (
                    <h3 className="detail-title pt-3">Báo chí nói về dự án</h3>
                  )
                }

              })()}
              <div className="row">

                {
                  news_papers.newsPapers &&
                  news_papers.newsPapers.map((prop, index) => (

                    <div className="col-md-4 col-xs-12" key={index}>
                      <div className="image">
                        <a href={prop.link} title={prop.title} target="_blank" rel="noopener noreferrer">
                          <FluidImage image={prop.image} />
                        </a>
                      </div>
                      <div className="content">
                        <h3 className="title">
                          <a href={prop.link} title={prop.title} target="_blank" rel="noopener noreferrer">
                            {prop.title}
                          </a>
                        </h3>
                        <div className="excerpt">
                          <div
                            key={`body`}
                            id="___gatsby"
                            dangerouslySetInnerHTML={{ __html: prop.excerpt }}
                          />
                        </div>
                      </div>
                    </div>
                  ))
                }
              </div>
            </div>
          </div>
          <div className="col-md-3 col-xs-12">
            <aside>
              {(() => {
                if (CustomPost.address != null) {
                  return (
                    <>
                      <div className="contact-info text-center">
                        <div className="title-sidebar">
                          <h3>Hotline hỗ trợ khách hàng</h3>
                        </div>
                        {(() => {
                          if (CustomPost.phone != null) {
                            return (
                              <h2><a href={`tel:${CustomPost.phone}`}>{CustomPost.phone}</a></h2>
                            )
                          }
                          else {
                            return (
                              <h2><a href={`tel:0911234999`}> 0911 234 999</a></h2>
                            )
                          }

                        })()}

                      </div>
                      <PostForm pageContext={pageContext} />
                    </>
                  )
                }
              })()}

              <Project />
            </aside>
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Post
